<template>
  <Suspense>
    <template #default>
      <ExampleTransfer :menu="teams1"/>
    </template>
    <template #fallback>
      <LoaderComponentTwo/>
    </template>
  </Suspense>
</template>

<script>
import{reactive,onMounted} from "vue";
import {defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
import {handleError} from "@/components/composable/handleError";
const ExampleTransfer=defineAsyncComponent(()=>import('@/components/main/transfer/ExampleTransfer'))
const LoaderComponentTwo=defineAsyncComponent(()=>import('@/components/composable/LoaderComponentTwo'))
export default {
  name: "AllLeage",
  components: {LoaderComponentTwo, ExampleTransfer},
  setup(){
    const teams1=reactive([])
    const sum=onMounted(async ()=>{
      await EndPoints.getTransAll()
          .then((response)=>{
            if(response.data.success){
              teams1.push(response.data.data)
            }
          })
          .catch((error)=>{
            handleError(error)
          })
    })
    return{
      teams1,
      sum,
    }
  }
}
</script>

